import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { YeuCauSuDungPTNModel } from 'src/app/shared/models/yeu-cau-su-dung-ptn.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { DanhSachSinhVienService } from './danh-sach-sinh-vien.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-danh-sach-sinh-vien',
  templateUrl: './danh-sach-sinh-vien.component.html',
  styleUrls: ['./danh-sach-sinh-vien.component.scss'],
})
export class DanhSachSinhVienComponent implements OnInit {
  _length = 0;
  displayedColumns: string[] = [
    'STT',
    'HoTen',
    'NgaySinh',
    'MaSo',
    'LoaiHocVien',
    'SDT',
    'Email',
    'TenDeTai',
    'TenGVHD',
    'ThoiGian',
    'TenNoiQuanLy',
    'TenHVCH_NCS',
    'MaHVCH_NCS',
  ];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __service: DanhSachSinhVienService
  ) {}
  _yeuCauSuDungPTNModels: YeuCauSuDungPTNModel[];
  dataSource: MatTableDataSource<YeuCauSuDungPTNModel>;
  showProgressBar = false;
  ngOnInit(): void {
    this.showProgressBar = true;
    this.getDatas();
  }
  delay(ms: number) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  private getDatas() {
    this.showProgressBar = true;
    this.__service.getAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        var stt = 0;
        
        data.sort((a, b): number => {
          return (
            new Date(a.ThoiGianBatDau).getTime() -
            new Date(b.ThoiGianBatDau).getTime()
          );
        });
        data.map((item) => {
          if (new Date(item.ThoiGianKetThuc).getTime() < new Date().getTime())
            item.IsOver = true;
          item.STT = ++stt;
          return item;
        });
        this._yeuCauSuDungPTNModels = data;
        this.dataSource = new MatTableDataSource(this._yeuCauSuDungPTNModels);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        // this.dataSource.filterPredicate =
        //   (data: LichSuMuonThietBiModel, filter: string) => (
        //     data.MaThietBi.toLocaleLowerCase().indexOf(filter) != -1
        //     || data.HoTenSV.toLocaleLowerCase().indexOf(filter) != -1
      },
      (error) => {
        this.showProgressBar = false;
        // console.log(error)
        // this.dialog.open(AlertDialogComponent, {
        //   disableClose: true,
        //   width: '17rem',
        //   data: {
        //     dataKey: 'Có lỗi xảy ra'
        //   }
        // });
      }
    );
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
